	module.exports = {"data":{"post":{"html":"<p>I've been playing with Net News Wire, and it seems to be as good as everyone says it is. It's the first desktop news aggregator I've tried that makes me feel like I want to keep using it. But it's still just a desktop aggregator... I can't use it at work, and it only works when the machine is turned on and connected.</p>\n<p>Which got me thinking... wouldn't it be nice if news readers worked like IMAP mail clients? A server could collect the feeds and keep track of which items had been read, and I could use whatever reader I want from whatever machine I want. Just a thought...</p>","fields":{"title":"IMAP for aggregators","date":"09 May 2004","url":"https://papascott.de/2004-05-09-imap-for-aggregators/"}}},"pathContext":{"slug":"/2004-05-09-imap-for-aggregators/","prev":{"fields":{"title":"code: theWebSocket; - Apple Archives","date":"09 May 2004","slug":"/2004-05-09-code-thewebsocket-apple-archives/"}},"next":{"fields":{"title":"Nena was Emilia...","date":"09 May 2004","slug":"/2004-05-09-nena-was-emilia/"}}}}

